import React from 'react';
import { TodoContext } from '../TodoContext';
import './TodoForm.css';

function TodoForm(props){
    const [newTodoValue, setNewTodoValue] = React.useState('');
    
    const {
        addTodo,
        setOpenModal,
    } = React.useContext(TodoContext);

    const onSubmit = (event) => {
        event.preventDefault();
        addTodo(document.getElementById("todoText").value);
        setOpenModal(false);
    };

    const onChange = (event) => {
        setNewTodoValue(event.target.value);
    };

    const onCancel = () => {
        setOpenModal(false);
    };

    const onAdd= () => {

    };

    const checkEnter = (Event) => {
        if (Event.which == 13)  onSubmit(Event);
    }

    
    
    return (
        <form onSubmit={onSubmit}>
            <label>Escribe un todo</label>
            <textarea
                id='todoText'
                onKeyUp={checkEnter}
                placeholder="prepara canela para maura"
            />
            <div className='TodoForm-ButtonContainer'>
                <button
                    className='TodoForm-Button TodoForm-button-cancel' type='button'
                    onClick={onCancel}
                >
                    Cancelar
                </button>
                <button 
                    className='TodoForm-Button TodoForm-button-add'
                    type='submit'
                    onClick={onAdd}
                >
                    Añadir
                </button>
            </div>
        </form>
    );
}

export {TodoForm};